import React from "react";

import cv from "../assets/json/cv.json";

const DadosPessoais = () => {
  const widthDiv = {
    width: "49%"
  };

  const widthTh = {
    width: "25%"
  };

  const dadosPessoais = cv.dadosPessoais;

  return (
    <div
      className="bg-dark mr-md-3 pt-3 px-3 pt-md-5 px-md-5 text-center text-white overflow-hidden"
      style={widthDiv}
    >
      <div className="my-3 py-3">
        <h2 className="display-5">{dadosPessoais.titulo}</h2>
        <p className="lead">{dadosPessoais.mensagem}</p>
      </div>
      <div className="bg-light shadow-sm mx-auto box-interno py-3 px-4">
        <table className="table table-hover text-left ">
          <tbody>
            <tr>
              <th style={widthTh}>E-mail:</th>
              <td colSpan="5">{dadosPessoais.email}</td>
            </tr>
            <tr>
              <th>Endereço:</th>
              <td colSpan="5">{dadosPessoais.endereco}</td>
            </tr>
            <tr>
              <th>Telemovel:</th>
              <td colSpan="5">{dadosPessoais.telemovel}</td>
            </tr>
            <tr>
              <th>Estado civil:</th>
              <td colSpan="5">{dadosPessoais.estadoCivil}</td>
            </tr>
            <tr>
              <th>Residência:</th>
              <td>{dadosPessoais.residencia.status}</td>
              <th>Nif:</th>
              <td>{dadosPessoais.nif.status}</td>
              <th>Niss:</th>
              <td>{dadosPessoais.niss.status}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default DadosPessoais;
