import React, { Fragment } from "react";

import cv from "../assets/json/cv.json";

const Idiomas = () => {
  const widthDiv = {
    width: "49%"
  };

  const idiomas = cv.idiomas;

  return (
    <div
      class="bg-light mr-md-3 pt-3 px-3 pt-md-5 px-md-5 text-center overflow-hidden"
      style={widthDiv}
    >
      <div class="my-3 py-3">
        <h2 class="display-5">{idiomas.titulo}</h2>
        <p class="lead">{idiomas.mensagem}</p>
      </div>

      <div class="bg-dark shadow-sm mx-auto box-interno py-4 px-4">
        <table className="table table-hover table-dark ">
          <thead>
            <tr>
              <th scope="col">Idioma</th>
              <th scope="col">Nivel</th>
            </tr>
          </thead>
          <tbody>
            {idiomas.dados.map(lingua => (
              <Fragment>
                <tr>
                  <td>{lingua.idioma}</td>
                  <td>{lingua.nivel}</td>
                </tr>
              </Fragment>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Idiomas;
