import React, { Fragment } from "react";

import cv from "../assets/json/cv.json";

const RedesSociais = () => {
  const widthDiv = {
    width: "49%"
  };

  const redesSociais = cv.redesSociais;

  return (
    <div
      className="bg-dark mr-md-3 pt-3 px-3 pt-md-5 px-md-5 text-center text-white overflow-hidden"
      style={widthDiv}
    >
      <div className="my-3 py-3">
        <h2 className="display-5">{redesSociais.titulo}</h2>
        <p className="lead">{redesSociais.mensagem}</p>
      </div>

      <div className="bg-light shadow-sm mx-auto box-interno py-3 px-4">
        <table className="table table-hover">
          <thead>
            <tr>
              <th scope="col">Redes</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {redesSociais.midias.map(rede => (
              <Fragment>
                <tr>
                  <td>{rede.nome}</td>

                  <th scope="row">
                    <a className="btn btn-link" href={rede.nome} role="button">
                      Ir
                    </a>
                  </th>
                </tr>
              </Fragment>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default RedesSociais;
