import React, { Fragment } from "react";

import cv from "../assets/json/cv.json";

const Portfolio = () => {
  const widthDiv = {
    width: "49%"
  };

  const widthTh = {
    width: "25%"
  };

  const portfolios = cv.portfolios;

  return (
    <div
      className="bg-light mr-md-3 pt-3 px-3 pt-md-5 px-md-5 text-center overflow-hidden"
      style={widthDiv}
    >
      <div className="my-3 p-3">
        <h2 className="display-5">{portfolios.titulo}</h2>
        <p className="lead">{portfolios.mensagem}</p>
      </div>

      <div className="bg-dark shadow-sm mx-auto box-interno py-3 px-4">
        <table className="table table-hover table-dark table-sm ">
          <caption className="text-right">
            <a className="btn btn-link" href={portfolios.url} role="button">
              Ver mais ...
            </a>
          </caption>
          <thead>
            <tr>
              <th scope="col">Nome</th>
              <th scope="col">Descrição</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {portfolios.portfolio.map(port => (
              <Fragment>
                <tr>
                  <th style={widthTh}>{port.nome}</th>
                  <td>{port.descricao}</td>
                  <td>
                    <a
                      className="btn btn-link"
                      href={port.url}
                      target="_blank"
                      rel="noopener noreferrer"
                      role="button"
                    >
                      Mostrar
                    </a>
                  </td>
                </tr>
              </Fragment>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Portfolio;
