import React from "react";

import cv from "../assets/json/cv";

const Inicio = () => {
  const inicio = cv.inicio;

  const imagem = require("../assets/images/avatar.jpg");

  return (
    <div className="jumbotron">
      <div className="container">
        <div className="media">
          <img
            className="bd-placeholder-img rounded-circle mr-3"
            width="150"
            height="150"
            src={imagem}
            alt="my avatar"
          />
          <div className="media-body">
            <h1 className="display-4">{inicio.nome}</h1>
            <p>{inicio.mensagem}</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Inicio;
